.banner{
    margin-top:-10vh;
    height:114vh;
    margin-bottom: -80px;
    color: white;
	object-fit: contain;
}
.banner__contents {
	margin-left: 30px;
	padding-top: 300px;
}
.banner__title {
	font-size: 3rem;
	font-weight: 800;
	padding-bottom: 0.3rem;
}
.banner__description {
	width: 45rem;
	line-height: 1;
	padding: 1rem 0;
	font-size: 1rem;
	max-width: 360px;
	height: 80px;
}
.banner__button {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-size: 1.2rem;
  font-weight: 700;
  border-radius: 0.2vw;
  padding: 0.3rem 1.3rem;
  margin-right: 1rem;
  background-color: rgba(51, 51, 51, 0.5);
}

.banner__play {
  display: flex;
  align-items: center;
  color: #000;
  background-color: #fff;
  gap: 10px;
}

.banner__play img {
  width: 25px;
}

.banner__play p {
  font-size: 15px;
}

.banner__info {
  display: flex;
  align-items: center;
  color: #000;
  background-color: #818181;
  gap: 10px;
}

.banner__info img {
  width: 25px;
}

.banner__info p {
  font-size: 15px;
  font-weight: 600;
  color: #fff;
}

.banner__button:hover {
  color: #000;
  background-color: #c6c6c6;
  transition: all 0.2s;
}

.banner__fadeBottom {
  height: 55vh;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgb(37, 37, 37, 0.91),
    #111
  );
}
.banner__fadeBottom {
	height: 40vh;
	background-image: linear-gradient(
		180deg,
		transparent,
		rgb(37, 37, 37, 0.91),
		#111
	);
}

@media screen and (max-width: 768px){
	.banner__description {
		width: 100%;
		font-size: 12px;
		margin-left: -5px;
	}
	.banner__title {
		font-size: 2rem;
		font-weight: 800;
		padding-bottom: 0.3rem;
	}
	.banner__button {
		font-size: 1rem;
		font-weight: 700;
		padding: 1rem 2rem;
		margin-right: 2rem;
	}
	
}